.Main {
    background-color: white;
    z-index: 1;
    position: absolute;
    top: 100%;
    padding-top: 2em;
    width: 100%;

    // &__dummy {
    //     padding: 1em;
    //     margin-bottom: 0;
    // }

    &__photos {

        @media(min-width: 1024px) {
            max-width: 1200px;
            margin-left: auto;
            margin-right: auto;
            display: flex;
            flex-wrap: wrap;
        }
    }

    &__photo {
        overflow: hidden;
        flex-basis: 50%;
    }
}